import { Component, OnInit, Input, NgZone } from '@angular/core';
import {ConsultationService} from '../consultation.service';
import { Router, ActivatedRoute } from '@angular/router';
import { SocketEventsService } from '../socket-events.service';
import {ConfirmationService } from '../confirmation.service';
@Component({
  selector: 'app-consultation',
  templateUrl: './consultation.component.html',
  styleUrls: ['./consultation.component.scss']
})
export class ConsultationComponent implements OnInit {

  @Input() consultation;
  consultationId;
  token;
  incomingCall = false;
  user;
  callEvent;
  confirmClose = false;
  redirect;
  audioOnly = false;
  constructor(private conServ: ConsultationService, private activeRoute: ActivatedRoute, private router: Router,
              private _socketEventsService: SocketEventsService,
              private zone: NgZone,
              private confirmationServ: ConfirmationService
    ) {

    this.callEvent  = {};
    if (this.router.getCurrentNavigation() && this.router.getCurrentNavigation().extras.state) {
      this.callEvent = this.router.getCurrentNavigation().extras.state;
    }

  }

  ngOnInit() {
    this.consultationId = this.activeRoute.snapshot.params.id;
    this.getConsultation();
    this.callEvent.id = this.callEvent._id = this.consultationId;
    if (this.callEvent.token) {
      this.incomingCall = true;
    }

    this._socketEventsService.onCall().subscribe(e => {
      console.log('call event >>>>>>>>>>>>>>> ', e);
      this.zone.run(() => {
        if (e.data.consultation === this.consultationId) {
          this.callEvent = e.data;
          this.callEvent.id = this.consultationId;
          this.incomingCall = true;
        }
      });
    });

    this.confirmationServ.getConfirmation().subscribe(redirect => {
      this.redirect = redirect;
      this.confirmClose = true;
    });
  }

  getConsultation() {
    this.conServ.getConsultation(this.consultationId).subscribe(consultation => {
      console.log('got consultation ', consultation);
      this.consultation = consultation;
    });
  }


  makeCall(audioOnly) {

    this.audioOnly = audioOnly;
    console.log('make call ', this.consultation);
    this.incomingCall = true;
    this.token = null;
  }
  reject() {
    console.log('call rejected >>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>')
    this.zone.run(() => {
      this.incomingCall = false;
    })

    this.callEvent.token = undefined;
  }

}
