import { Component, OnInit, Input, NgZone, ViewChild, ElementRef, EventEmitter, Output } from '@angular/core';
import { MessageService } from '../message.service';
import { AuthService } from '../auth/auth.service';
import { SocketEventsService } from '../socket-events.service';
import { ConsultationService } from '../consultation.service';
import { environment } from '../../environments/environment';

import { ScrollEvent } from 'ngx-scroll-event';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.scss']
})
export class ChatComponent implements OnInit {
  @Input() consultation;
  @Input() showInput: boolean;
  @ViewChild('scoll') contentArea: ElementRef;
  @ViewChild('fileInput') fileInput: ElementRef;

  currentUser;
  chatMessages = [];
  chatText: string;
  fileToUpload: File = null;

  loadingMsgs = true;
  @Input() noPagination = false;
  @Output() loaded = new EventEmitter<boolean>();

  loadedImages = 0;
  chatImagesCount;
  constructor(private msgServ: MessageService,
              private authService: AuthService,
              private socketEventsService: SocketEventsService,
              private zone: NgZone,
              private consultationService: ConsultationService,
  ) { }

  ngOnInit() {

    this.loaded;
    this.currentUser = this.authService.currentUserValue;

    this.getMessages();
    this.socketEventsService.onMessage().subscribe(msg => {
      if (msg.data.consultation !== (this.consultation._id || this.consultation.id)) { return; }

      this.zone.run(() => {
        this.chatMessages.push(this.adjustMsg(msg.data));

        this.scrollToBottom();
      });
    });


  }

  async getMessages(noScroll?) {
    this.loadingMsgs = true;

    this.msgServ.getConsultationMessages(this.consultation._id || this.consultation.id,
      this.chatMessages.length,
      this.noPagination).subscribe(msgs => {
        this.zone.run(() => {

          this.chatMessages = msgs.map(m => {
            return this.adjustMsg(m);
          }).concat(this.chatMessages);

          this.chatImagesCount = this.chatMessages.filter(msg => msg.isImage).length;
          this.loadingMsgs = false;

          if (this.noPagination && !this.chatImagesCount) {
            setTimeout(() => {
              this.addPageDividers();
            }, 0);

          }

          if (!noScroll) {
            this.scrollToBottom(100);
          }

        });
      });

  }
  scrollToBottom(after?): void {
    try {
      setTimeout(() => {
        console.log('scroll to bottom');
        this.contentArea.nativeElement.scrollTop = this.contentArea.nativeElement.scrollHeight;
      }, after || 10);

    } catch (err) { }
  }

  send(e) {
    this.chatText = this.chatText.trim();
    if (this.chatText === '') {
      return;
    }

    this.chatMessages.push({
      direction: 'outgoing',
      text: this.chatText,
      createdAt: Date.now()
    });

    this.scrollToBottom();
    this.msgServ.sendMessage(this.consultation.id || this.consultation._id, this.chatText).subscribe(r => {
    });

    this.chatText = '';

  }

  sendMsg(event) {
    if (event.charCode === 13) {
      this.send(event);
      return false;
    }
  }
  readMessages() {
    this.consultationService.readMessages(this.consultation.id || this.consultation._id);
  }

  adjustMsg(msg) {

    if (msg.type === 'attachment') {
      msg.attachmentsURL = environment.api +
        `/consultation/${(this.consultation._id || this.consultation.id)}/attachment/${msg.id}?token=${this.currentUser.token}`;

      if (msg.mimeType.endsWith('jpeg') || msg.mimeType.endsWith('png')) {
        msg.isImage = true;

      } else if (msg.mimeType.startsWith('audio')) {
        msg.isAudio = true;
      } else {
        msg.isFile = true;
      }
    }

    if (msg.from === this.currentUser.id) {
      msg.direction = 'outgoing';
    } else {
      msg.direction = 'incoming';
    }
    return msg;
  }

  download(file) {
    window.location = file;
  }

  showAttachBrowseDlg() {

    const event = new MouseEvent('click', { bubbles: true });
    this.fileInput.nativeElement.dispatchEvent(event);

  }

  handleFileInput(event) {
    if (!event.target.files.item(0)) { return; }
    this.consultationService.postFile(event.target.files.item(0), this.consultation.id || this.consultation._id).subscribe(data => {

      this.zone.run(() => {
        this.chatMessages.push(this.adjustMsg(data.message));
        this.scrollToBottom(100);
      });
    });
  }

  public handleScroll(event: ScrollEvent) {

    if (event.isReachingTop && !this.loadingMsgs && !this.noPagination) {
      this.getMessages(true);
    }

  }



  addPageDividers() {
    let page = 1;
    this.chatMessages.forEach(msg => {
      const elem = document.getElementById(msg.id);
      console.log('add divider ', elem, elem.offsetTop);

      if (!elem) { return; }
      if (elem.offsetTop > ((2970 * page) - 720)
        && elem.offsetTop < ((2970 * page))) {
        elem.style['margin-top'] = '700px';
        page++;
      }
    });
    this.loaded.emit(true);
  }


  sleep(ms) {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        return resolve();
      }, ms);
    });

  }

  imageLoaded() {
    this.loadedImages++;
    if (this.loadedImages === this.chatImagesCount && this.noPagination) {
      this.addPageDividers();

    }
  }
}
