import { first } from 'rxjs/operators';
import { Subscription } from 'rxjs';
import { InviteService } from './../invite.service';
import { Component, Inject, OnDestroy } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormControl, FormGroupDirective, NgForm, Validators, ValidatorFn, FormBuilder, FormGroup } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';

enum Gender {
  male = 'male',
  female = 'female'
}
interface DialogData {
  phoneNumber: string;
  emailAddress: string;
  firstName: string;
  lastName: string;
  gender: Gender;
}



export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}
@Component({
  selector: 'app-invite-form',
  templateUrl: './invite-form.component.html',
  styleUrls: ['./invite-form.component.scss']
})
export class InviteFormComponent implements OnDestroy {

  matcher = new MyErrorStateMatcher();
  loading = false;
  createInviteSub: Subscription;
  error = '';
  myForm;
  genders = [Gender.male, Gender.female];


  constructor(
    public dialogRef: MatDialogRef<InviteFormComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData, private inviteService: InviteService, private formBuilder: FormBuilder) {
    this.createFormGroup();
  }


  createFormGroup() {
    this.myForm = this.formBuilder.group({
      emailFormControl: new FormControl('', [Validators.email]),
      phoneNumberFormControl: new FormControl('', [Validators.pattern(new RegExp(/^\+[0-9 ]+$/))]),
      firstNameFormControl: new FormControl('', [Validators.required]),
      lastNameFormControl: new FormControl('', [Validators.required]),
      genderFormControl: new FormControl('', [Validators.required])
      // our custom validator
    }, { validator: this.atLeastOnePhoneRequired });

    (window as any).myForm = this.myForm;
  }

  atLeastOnePhoneRequired(group: FormGroup): { [s: string]: boolean } {


    if (group) {
      if (group.controls.emailFormControl.value || group.controls.phoneNumberFormControl.value) {
        return null;
      }
    }
    if (group.controls.emailFormControl.touched || group.controls.phoneNumberFormControl.touched) {
      return { error: true };
    }
    return null;
  }

  onNoClick(): void {
    this.dialogRef.close();
  }
  onSubmit() {


    if (!this.data.emailAddress && !this.data.phoneNumber) {
      console.log('set errors ');

      this.myForm.setErrors({ ...this.myForm.errors, error: true });

    }
    if (this.myForm.pristine || !this.myForm.valid) {
      this.validateAllFormFields(this.myForm);
      return;
    }

    this.loading = true;
    this.createInviteSub = this.inviteService.createInvitation(this.data).subscribe(res => {

      this.loading = false;
      this.dialogRef.close();
    }, err => {
      this.loading = false;

      this.error = err;
    });
  }

  ngOnDestroy() {
    if (this.createInviteSub) {
      this.createInviteSub.unsubscribe();
    }
  }

  validateAllFormFields(formGroup: FormGroup) {         // {1}
    Object.keys(formGroup.controls).forEach(field => {  // {2}
      const control = formGroup.get(field);             // {3}
      if (control instanceof FormControl) {             // {4}
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {        // {5}
        this.validateAllFormFields(control);            // {6}
      }
    });
  }
}
