import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { catchError } from 'rxjs/operators';
import { throwError as observableThrowError } from 'rxjs';
import { environment } from '../environments/environment'
@Injectable({
  providedIn: 'root'
})
export class OpenViduService {


  constructor(private http: HttpClient) {

  }

  getToken(mySessionId: string, audioOnly): Promise<any> {
    return this.createSessionAndGetToken(mySessionId, audioOnly);
  }

  createSessionAndGetToken(sessionId: string, audioOnly): Promise<any>  {
    return new Promise((resolve, reject) => {

      const body = JSON.stringify({ customSessionId: sessionId });
      const options = {
        headers: new HttpHeaders({
          'Content-Type': 'application/json',

        })
       };
      return this.http.post<any>(environment.api + '/consultation/' + sessionId + '/call?audioOnly='+ audioOnly, body, options)
        .pipe(
          catchError(error => {
            error.status === 409 ? resolve(sessionId) : reject(error);
            return observableThrowError(error);
          })
        )
        .subscribe(response => {
          console.log('response >> ', response);
          resolve(response);
        });
    });
  }

  rejectCall(consultation: string, message): Promise<any>  {
    return new Promise((resolve, reject) => {

      const body = JSON.stringify({  });
      const options = {

        headers: new HttpHeaders({
          'Content-Type': 'application/json',

        })
       };
      return this.http.post<any>(environment.api + '/consultation/' + consultation + '/' + message + '/reject-call', {}, options)
        .subscribe(response => {
          console.log('response >> ', response);
          resolve();
        });
    });
  }

  acceptCall(consultation: string, message): Promise<any>  {
    return new Promise((resolve, reject) => {

      const body = JSON.stringify({  });
      const options = {

        headers: new HttpHeaders({
          'Content-Type': 'application/json',

        })
       };
      return this.http.post<any>(environment.api + '/consultation/' + consultation + '/' + message + '/accept-call', {}, options)
        .subscribe(response => {
          console.log('response >> ', response);
          resolve();
        });
    });
  }

}
