import { Component, OnInit, NgZone, OnDestroy, ViewChild , ElementRef } from '@angular/core';
import { ConsultationService } from '../consultation.service';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '../auth/auth.service';
import * as html2canvas from 'html2canvas';
(window as any).html2canvas = html2canvas;

import jsPDF from 'jspdf';



@Component({
  selector: 'app-consultations',
  templateUrl: './consultations.component.html',
  styleUrls: ['./consultations.component.scss']
})
export class ConsultationsComponent implements OnInit, OnDestroy {

  @ViewChild('chatHistory') chatHistory: ElementRef;

  unreadPendingCount = 0;
  consultations = [];
  unreadActiveCount = 0;
  status;
  overviewSub;
  unreadActiveCountSub;
  unreadPendingCountSub;
  unreadCount;
  currentConsultation;
  chatOpen;
  unreadClosedCountSub;
  title: {
    title,
    icon
  };
  exportedCon;
  titles = [{
    status: 'pending',
    title: 'Salle d\'attente',
    icon: 'queue'
  }, {
    status: 'active',
    title: 'Consultations ouvertes',
    icon: 'chat'
  }, {
    status: 'closed',
    title: 'Historique des consultations',
    icon: 'history'
  }];

  currentUser;
  PDFConsultation;
  constructor(private consultationService: ConsultationService,
              private router: Router,
              private activatedRoute: ActivatedRoute,
              private zone: NgZone,
              private authService: AuthService

              ) { }

  ngOnInit() {


    this.currentUser = this.authService.currentUserValue;
    console.log('current user ', this.currentUser);
    this.status = this.activatedRoute.snapshot.data.status;
    this.title = this.titles.find(t => t.status === this.status);
    this.getConsultations();
    this.getUnreadCount();

  }
  getConsultations() {
    this.overviewSub = this.consultationService.getConsultationsOverview().subscribe(consultations => {

      console.log('got consultation over ', consultations);
      this.zone.run(() => {

        this.consultations = consultations.filter(c => c.consultation.status === this.status);
        if (this.status === 'closed') {
          this.consultations = this.consultations.sort((a, b) => {
            return b.consultation.closedAt - a.consultation.closedAt;
          });
        }
        console.log(this.consultations);
      });
    });
  }

  getUnreadCount() {

    if (this.status === 'active') {

      this.unreadActiveCountSub = this.consultationService.unreadActiveCount().subscribe(count => {
        this.zone.run(() => {
          this.unreadCount = count;
        });
      });
    }

    if (this.status === 'pending') {

      this.unreadPendingCountSub = this.consultationService.unreadPendingCount().subscribe(count => {
        this.zone.run(() => {
          this.unreadCount = count;
        });
      });
    }

  }
  showChat(id) {
    // if (this.status === 'closed') {
    //   return;
    // }
    this.currentConsultation = this.consultations.find(c => c._id === id);

    this.chatOpen = true;
  }
  ngOnDestroy() {
    if (this.overviewSub) {
      this.overviewSub.unsubscribe();

    }
    if (this.status === 'active') { if (this.unreadActiveCountSub) { this.unreadActiveCountSub.unsubscribe(); } }
    if (this.status === 'pending') { if (this.unreadPendingCountSub) { this.unreadPendingCountSub.unsubscribe(); } }

  }

  exportPDF(consultation) {
    this.PDFConsultation = consultation;
  }


 async generateAndSavePDF() {

   const filename  = `${this.PDFConsultation._id}.pdf`;
   const pagesCount = Math.floor(this.chatHistory.nativeElement.offsetHeight / 2970) + 1;
   console.log('pages count ', pagesCount);
   const pdf = new jsPDF('p', 'mm', 'a4', true);
   for (let page = 0; page < pagesCount; page++) {

      const canvas = await html2canvas(document.querySelector('#chatHistory'), { letterRendering: 1,
         y: (page * 2970) - document.querySelector('.mat-drawer-content.mat-sidenav-content').scrollTop,
           height: 2970, useCORS : true});
      pdf.addImage(canvas.toDataURL('image/png'), 'PNG', 0, 0, 211, 297, '', 'MEDIUM');

      if (page === pagesCount - 1) {
         pdf.save(filename);
         console.log(typeof pdf.output('arraybuffer'));
         this.consultationService.sendReport( new File([pdf.output('arraybuffer')],
          'report.pdf', {type: 'application/pdf'}), this.PDFConsultation._id ).subscribe(r => {
           console.log('file saved ');
         });
         this.PDFConsultation = null;
        // console.log('pdf output ')
         return;
      }
      pdf.addPage();

  }
  }
}
