import { InviteService } from './../invite.service';
import { InviteFormComponent } from './../invite-form/invite-form.component';
import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-invitations',
  templateUrl: './invitations.component.html',
  styleUrls: ['./invitations.component.scss']
})
export class InvitationsComponent implements OnInit {
  animal: string;
  name: string;

  invitations = [];

  constructor(public dialog: MatDialog, private inviteService: InviteService) { }

  ngOnInit() {
    this.getInvites();
  }

  openDialog(): void {
    const dialogRef = this.dialog.open(InviteFormComponent, {
      width: '500px',
      height: '600px',
      data: {}
    });

    dialogRef.afterClosed().subscribe(result => {

      this.getInvites();
    });
  }

  getInvites() {

    this.inviteService.getInvitations().subscribe(invitations => {
      this.invitations = invitations;
    });
  }

  resendInvite(id) {

    const invite = this.invitations.find(i => i.id === id);
    invite.resending = true;
    this.inviteService.resendInvite(id).subscribe(res => {
      invite.resending = false;

    });
  }

  revokeInvite(id) {
    const invite = this.invitations.find(i => i.id === id);

    invite.revoking = true;
    this.inviteService.revokeInvite(id).subscribe(res => {
      invite.revoking = false;
      this.getInvites();
    });

  }

}
