import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { environment } from '../environments/environment';

import { Router } from '@angular/router';
@Injectable({
  providedIn: 'root'
})
export class SocketEventsService {

  initialized = false;
  constructor(private router: Router) {

  }

  init(currentUser) {
    // console.log('sails ' , io.sails)
    console.log('initialize socket ', this.initialized);
    if (this.initialized) { return; }
    io.sails.query = `token=${currentUser.token}`;
    io.sails.headers = { id: currentUser.id, 'x-access-token' : currentUser.token };
    const socket = io.sails.connect(environment.host, {  reconnection: true,
    reconnectionDelay: 1000,
    reconnectionDelayMax : 5000
  });
    (window as any).socket = socket;

    // window.socket
    socket.on('connect', function socketConnected() {


      socket.get('/api/v1/subscribe-to-socket', {}, function(resData, jwres) {
        // ...
        console.log('res data ', resData, jwres);
      });
      socket.get('/api/v1/subscribe-to-doctors', {}, function(resData, jwres) {
        // ...
        console.log('res data ', resData, jwres);
      });
    }.bind(this));

    this.initialized = true;
  }

  onConsultation(): Subject<any> {

    const sub = new Subject();
    const obs = Observable.create(observer => {
      socket.on('newConsultation', e => {

        this.playAudio();
        observer.next(e);
      });
    });
    obs.subscribe(sub);
    return sub;
  }
  onMessage(): Subject<any> {
    const sub = new Subject();
    const obs = Observable.create(observer => {
      socket.on('newMessage', e => { console.log('new mesg ', e); observer.next(e); });
    });
    obs.subscribe(sub);
    return sub;
  }
  onCall(): Subject<any> {
    const sub = new Subject();
    const obs = Observable.create(observer => {
      console.log('call event');
      socket.on('newCall', e =>  { console.log('new call ', e); observer.next(e); });
    });
    obs.subscribe(sub);
    return sub;
  }
  onRejectCall(): Subject<any> {
    const sub = new Subject();
    const obs = Observable.create(observer => {
      console.log('reject call event');
      socket.on('rejectCall', e => observer.next(e));
    });
    obs.subscribe(sub);
    return sub;
  }
  onConsultationAccepted() {
    const obs = Observable.create(observer => {


      socket.on('consultationAccepted', e => {
        console.log('consultation accepted');
        observer.next(e);
      });
    });

    return obs;
  }

  onConsultationCanceled() {
    const obs = Observable.create(observer => {


      socket.on('consultationCanceled', e => {
        console.log('consultation canceled');
        observer.next(e);
      });
    });

    return obs;
  }
  playAudio() {
    const audio = new Audio();
    audio.src = '../assets/sounds/notification.mp3';
    audio.load();
    audio.play();
  }
}
