import { InviteFormComponent } from './invite-form/invite-form.component';
import { InvitationsComponent } from './invitations/invitations.component';
import { SupportComponent } from './support/support.component';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule, LOCALE_ID } from '@angular/core';

import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ErrorStateMatcher, ShowOnDirtyErrorStateMatcher } from '@angular/material';

import { HttpClientModule } from '@angular/common/http';


import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { JwtInterceptor } from './auth/jwt.interceptor';
import { ErrorInterceptor } from './auth/error.interceptor';
import { AuthService } from './auth/auth.service';
import { LoginComponent } from './login/login.component';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from './_guards/auth.guard';
import { RouterGuard } from './_guards/router.guard';

import { SharedModule } from './shared.module';

import { OpenViduVideoComponent } from './stream/ov-video.component';
import { VideoRoomComponent } from './video-room/video-room.component';
import { StreamComponent } from './stream/stream.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { ConsultationsComponent } from './consultations/consultations.component';
import { ConsultationComponent } from './consultation/consultation.component';
import { TopNavComponent } from './top-nav/top-nav.component';
import { SideChatComponent } from './side-chat/side-chat.component';
import { ChatComponent } from './chat/chat.component';
import { OverlayComponent } from './overlay/overlay.component';
import { ScrollEventModule } from 'ngx-scroll-event';


import { MsgTimePipe } from './msg-time.pipe';
import { DurationPipe } from './duration.pipe';

import { registerLocaleData } from '@angular/common';

import localeFr from '@angular/common/locales/fr';
registerLocaleData(localeFr);

const routes: Routes = [
  {
    path: '',

    children: [
      {
        path: '',
        redirectTo: 'dashboard',
        pathMatch: 'full'
      },
      {
        path: 'dashboard',
        component: DashboardComponent,
        data: {
          title: 'Dashboard'
        },
        canActivate: [RouterGuard]
      },
      {
        path: 'support',
        component: SupportComponent,
        data: {
          title: 'Support'
        },
        canActivate: [RouterGuard]
      },
      {
        path: 'invitations',
        component: InvitationsComponent,
        data: {
          title: 'Invitations'
        },
        canActivate: [RouterGuard]
      },
      {
        path: 'pending-consultations',
        component: ConsultationsComponent,
        data: {
          title: 'Consultations',
          status: 'pending'
        },
        canActivate: [RouterGuard]
      }, {
        path: 'active-consultations',
        component: ConsultationsComponent,
        data: {
          title: 'Consultations',
          status: 'active'
        },
        canActivate: [RouterGuard]
      }, {
        path: 'closed-consultations',
        component: ConsultationsComponent,
        data: {
          title: 'Consultations',
          status: 'closed'
        },
        canActivate: [RouterGuard]
      }, {
        path: 'pending-consultations/:id',
        component: ConsultationComponent,
        data: {
          title: 'Consultations'
        },
        canActivate: [RouterGuard]
      }, {
        path: 'active-consultations/:id',
        component: ConsultationComponent,
        data: {
          title: 'Consultations'
        },
        canActivate: [RouterGuard]
      }, {
        path: 'consultation/:id',
        component: ConsultationComponent,
        data: {
          title: 'Consultations'
        },
        canActivate: [RouterGuard]
      }
    ],
    canActivate: [AuthGuard, RouterGuard]
  }, {
    path: 'login',
    component: LoginComponent
  }, {
    path: '**',
    redirectTo: '/dashboard',
    pathMatch: 'full',
    canActivate: [AuthGuard, RouterGuard]
  }

];


@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    OpenViduVideoComponent,
    VideoRoomComponent,
    StreamComponent,
    DashboardComponent,
    ConsultationsComponent,
    ConsultationComponent,
    TopNavComponent,
    SideChatComponent,
    MsgTimePipe,
    DurationPipe,
    ChatComponent,
    OverlayComponent,
    SupportComponent,
    InvitationsComponent,
    InviteFormComponent,

  ],
  imports: [

    BrowserAnimationsModule,
    BrowserModule,
    SharedModule,
    HttpClientModule,
    ScrollEventModule,
    RouterModule.forRoot(routes)
  ],

  providers: [
    AuthService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: JwtInterceptor,
      multi: true
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: ErrorInterceptor,
      multi: true
    },
    {
      provide: ErrorStateMatcher,
      useClass: ShowOnDirtyErrorStateMatcher
    }, {
      provide: LOCALE_ID,
      useValue: 'fr-FR'
    },
  ],
  bootstrap: [AppComponent],
  entryComponents: [InviteFormComponent]

})
export class AppModule { }
