
import { Component, OnInit, OnDestroy } from '@angular/core';
import {FormControl, FormGroupDirective, NgForm, Validators} from '@angular/forms';
import {ErrorStateMatcher} from '@angular/material/core';
import { Router, ActivatedRoute, RouterStateSnapshot } from '@angular/router';
import { first } from 'rxjs/operators';
import { Subscription } from 'rxjs';

import { environment } from '../../environments/environment';
import {AuthService} from '../auth/auth.service';
/** Error when invalid control is dirty, touched, or submitted. */
export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit, OnDestroy {


  emailFormControl = new FormControl('', [
    Validators.required,
    Validators.email,
  ]);
  passwordFormControl = new FormControl('', [
    Validators.required,
  ]);
  matcher = new MyErrorStateMatcher();

  loading = false;
  submitted = false;
  returnUrl: string;
  error = '';
  email: string;
  password: string;
  loginSub: Subscription;
  samlLoginUrl = `${environment.api}/login-saml`;

  constructor(
    private authService: AuthService,
    private route: ActivatedRoute,
    private router: Router
    ) {

     }

  ngOnInit() {

    // If the user is already logged in, redirect him
    if (this.authService.currentUser) {
      this.router.navigateByUrl('/dashboard');
    }

    // If we have a token in the URL, the user has been redirected after the SAML login
    if ('token' in this.route.snapshot.queryParams) {
      console.log('token ', this.route.snapshot.queryParams.token);

      console.log('return url ', this.route.snapshot.queryParams.returnUrl);
      this.returnUrl = this.route.snapshot.queryParams.returnUrl || '/dashboard';

      this.authService.login(this.route.snapshot.queryParams.token)
      .pipe(first())
      .subscribe(
          data => {
            this.router.navigate([this.returnUrl]);
          },
          error => {
              this.error = error;
              this.loading = false;
          });
    }
  }

  // When the user submits the local form
  onSubmit() {
    console.log('submit', this.email, this.password);
    this.loading = true;
    this.loginSub = this.authService.loginLocal(this.email, this.password).subscribe(res => {
      this.loading = false;
      this.router.navigate(['dashboard']);
    }, err => {
      this.loginSub.unsubscribe();
      this.loading = false;
      this.error = err;
    });
  }

  // When leaving the page
  ngOnDestroy() {
    if (this.loginSub) {
      this.loginSub.unsubscribe();
    }
  }


}
